import React from 'react';
import PropTypes from 'prop-types';
import Dimensions from 'react-dimensions';

class Hero extends React.Component {
  static propTypes = {
    backgroundImageNarrow: PropTypes.string,
    backgroundImageWide: PropTypes.string,
    children: PropTypes.node,
    containerWidth: PropTypes.number.isRequired,
    height: PropTypes.oneOfType([
      PropTypes.string,
      PropTypes.number,
    ]),
  };

  static defaultProps = {
    backgroundImageNarrow: '',
    backgroundImageWide: '',
    children: null,
    height: '80vh',
  };

  render() {
    const backgroundImage = (this.props.containerWidth < 769) ?
      `url('${this.props.backgroundImageNarrow}')` :
      `url('${this.props.backgroundImageWide}')`;
    return (
      <div
        style={{
          backgroundImage,
          backgroundSize: 'cover',
          backgroundRepeat: 'no-repeat',
          backgroundPosition: 'bottom center',
          // width: this.props.containerWidth,
          height: this.props.height,
        }}
      >
        {this.props.children}
      </div>
    );
  }
}

export default Dimensions()(Hero);



