import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import Typography from 'material-ui/Typography';

import TagArray from '../tag-array';
import '../../css/prism-red-light.css';

const propTypes = {

};

const styles = theme => ({
  root: {
    padding: 16,
  //   textAlign: 'left',
    color: theme.palette.primary.dark,
  //   marginTop: 20,
  },
  titleMain: {
    color: theme.palette.primary.dark,
  },
  bodyMain: {
    color: theme.palette.secondary.dark,
    fontFamily: theme.typography.fontFamily,
    fontWeight: theme.typography.fontWeight,
  },
});

const PostFull = (props) => {
  const { classes, html, path, title, tags } = props;
  return (
    <div className={classes.root}>
      <Typography variant="title" className={classes.titleMain} gutterBottom>{title}</Typography>
      <div className={classes.bodyMain} dangerouslySetInnerHTML={{ __html: html }} />
      <TagArray tags={tags} navClick />
    </div>
  );
};

PostFull.propTypes = propTypes;

export default withStyles(styles, { name: 'StyledPostFull' })(PostFull);



